import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { Card, Row, Col, Checkbox, message } from 'antd';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { getStudyFieldGroups, deleteStudyFieldGroups } from 'services/studyFieldGroup';
import routeConstant from 'routes/route/routeConstant';

class StudyFieldGroupPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      reload: 0,
    };
  }

  handleDelete = async id => {
    await deleteStudyFieldGroups({ id });
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };

  render() {
    const { reload } = this.state;
    const columns = [
      {
        title: 'Mã khối ngành',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Tên khối ngành',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Hiệu lực',
        dataIndex: 'isActive',
        key: 'isActive',
        render: (text, record) => {
          if (record.isActive === false) {
            return <Checkbox disabled />;
          }
          return <Checkbox checked disabled />;
        },
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`/#${routeConstant.DANH_MUC__DAO_TAO__KHOI_NGANH__NHAP_DU_LIEU}?id=${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card>
        <Row>
          <Col span={24}>
            <CartHeadAction>
              <BtnAddNew href={`/#${routeConstant.DANH_MUC__DAO_TAO__KHOI_NGANH__NHAP_DU_LIEU}`} />
            </CartHeadAction>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              service={getStudyFieldGroups}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <StudyFieldGroupPage />
  </Page>
);



